@font-face {
  font-family: "Coconat";
  src:
    url("./fonts/Coconat-Regular.otf") format("opentype"),
}

@font-face {
  font-family: "Coconat-Demi";
  src:
    url("./fonts/Coconat-Demi.otf") format("opentype"),
}


/*GENERAL*/

body {
  font-family: "Coconat";
}

.section {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#canvasContainer {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100dvh;
  z-index: 1;
}

/*NAVBAR*/

#navBarHome {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  height: 100px;
}

.linkNav {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: red solid 1px;
  width: 250px;
  height: 25px;
  font-weight: bold;
}

.linkNav:hover {
  background-color: red;
  color: white;
  border: transparent solid 1px;
}

#navBarHome a {
  color: red;
  text-decoration: none;
  font-size: 18px;
  z-index: 2
}

#homeLogo {
  width: 250px;
  height: 120px;
  background-size: 100% 100%;
}

/*ABOUT*/

#aboutText {
  z-index: 2;
  position: absolute;
  text-align: center;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: red;
  width: 350px;
  font-size: 16px;
}

.contacts {
  z-index: 2;
  top: 63%;
  transform: translateX(-50%);
  position: absolute;
}

#Email {
  left: 40%;
}

#Instagram {
  left: 50%;
}

#Linkedin {
  right: 36%;
}

.contacts a {
  font-family: "Coconat-Demi";
  color: red;
  font-size: 14px;
  text-decoration: underline;
}

.credits {
  font-family: "Coconat-Demi";
  z-index: 2;
  top: 72%;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  color: red;
  font-size: 12px;
}

.credits a {
  color: red;
}